'use strict'

import $ from 'jquery'
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'

export default function () {
  // eslint-disable-next-line
  $('.persons').each(function () {
    let $this = $(this)
    // eslint-disable-next-line
    let slider = new Swiper($this.find('.persons__container'), {
      speed: 600,
      slidesPerView: 6,
      grabCursor: true,
      spaceBetween: 21,
      breakpoints: {
        991: {
          slidesPerView: 'auto'
        }
      }
    })
  })
}



