'use strict'

function foo () {
  const icons = {}
  const requireIcon = require.context('../../svg', false, /\.svg$/)
  requireIcon.keys().forEach(fileName => {
    const iconConfig = requireIcon(fileName)
    const { id, viewBox } = iconConfig.default || iconConfig
    const iconName = fileName.replace(/^\.\/|\.\w+$/g, '')
    const [width, height] = viewBox.split(' ').slice(-2)
    icons[iconName] = { id, ratio: width / height }
  })
  const output = Object.keys(icons).map(name => {
    const { id, ratio } = icons[name]
    return `.${id} {width: ${ratio}em;}\n`
  }).join('')
  console.log(output)
  const container = document.getElementById('__SVG_SPRITE_NODE__')
  const styleNode = document.createElement('style')
  styleNode.innerHTML = output
  styleNode.type = 'text/css'
  container.insertBefore(styleNode, container.firstChild)
}

export default foo()



